.workContainer{
  width: 100%;
  position: relative;
  height: 100%;
}

.somewhereinGazaNote{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.topBar, .bottomBar {
  width: 100%;
}

.topBar {
  top: 0;
  z-index: 2;
  position: absolute;
  opacity: 98%;
}

.bottomBar {
  position: sticky;
  top: 100%;
  z-index: 1;
  opacity: 98%;
}

.workContainer::-webkit-scrollbar{
    display: none;
}

.scrollableNote{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  overflow-y: scroll;
}

.scrollableNote::-webkit-scrollbar{
  display: none;
}

.projectLink {
  color: rgba(255, 255, 255, 0);
}

.workAllLink {
    position: absolute;
    top: 427px;
    left: 30px;
    text-decoration: none;
    color: transparent;
}

.workDevice{
    width: 100%;
}

